* {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior:smooth;
}

body {
  background-color: darkslategrey;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-animation: fadeInAnimation ease 3s;
          animation: fadeInAnimation ease 3s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes fadeInAnimation {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
   }
}

@keyframes fadeInAnimation {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
   }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  width: 100%;
  overflow-x:hidden;
}

@font-face {
    font-family: chinese-dragon;
    src: url(/static/media/ChineseDragon.523f052e.ttf);
 }

ul {
    list-style : none;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

li {
    padding: 0 20px;
    display: flex;
    align-items: center;
}
.header-normal {
    width: 100%;
    height:60px;
    position: fixed;
    background-color:transparent;
    transition: all 0.1s ease;
    z-index: 3;
}

.header-scrolled {
    width: 100%;
    height: 60px;
    position: fixed;
    background-color:black;
    z-index: 3;
    transition: all 0.1s ease;
}

.navbar-container {
    display:flex;
    align-items: center;
    justify-content: space-between;
    height: inherit;
}

.navbar-container-logo {
    display:flex;
    padding-left: 25px;
    margin:0;
    align-items: center;
    justify-content: space-between;
}

.navbar-container-logo--ref {
    text-decoration: none;
    display: flex;
    align-items: center;
}

.navbar-container-logo--logo {
    font-family: chinese-dragon;
    color:yellow;
    font-size: 30px;
}

.navbar-container-logo--logo:hover {
    color:orange;
}

.navbar-container-logo--description {
    color:white;
    font-size: 16px;
    padding-left: 15px;
}

.navbar-container-menuitems {
    display:flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 3vw;
}

.navbar-container-menuitems--links {
    text-decoration: none;
    font-size: 18px;
    color: white;
    background-image: linear-gradient(#ffffff, #ffffff);
    background-size: 0% 0.1em;
    background-position-y: 100%;
    background-position-x: 0%;
    background-repeat: no-repeat;
    transition: background-size 0.2s ease-in-out;
}

.navbar-container-menuitems--links:hover {
    background-size: 100% 0.1em;
}

.navbar-container-menuitems--links:active {
    text-decoration: underline;
}


@media screen and (min-width : 2000px) {
    .header-normal {
        height: 6vh;
    }
    .header-scrolled {
        height: 6vh;
    }

    .navbar-container-logo{
        padding-left: 1vw;
    }

    .navbar-container-logo--logo {
        font-size: 1.6vw;
    }
    .navbar-container-logo--description{
        font-size: 0.9vw;
        padding-left: 1vw;
    }

    .navbar-container-menuitems--links {
        font-size: 1vw;
    }

    li {
        padding: 0 1vw;
    }
} 
.hmenu-container {
    display: flex;
    margin-right: 6vw;
    color: white;
    justify-content: center;
    align-items: center;
    padding: 1.2vw;
    border-radius: 10px;
    position: relative;
}

.hmenu-container.scrolled:hover {
    background-color: rgba(95, 92, 92, 0.2);
}

.hmenu-container:hover {
    background-color: rgba(0, 0, 0, 0.3);
}

.hmenu-dropdown {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: black;
    top: 100%;
    right: -1300%;
    z-index: 3;
    -webkit-animation: slide 0.5s forwards;
    -webkit-animation-delay: 0s;
    animation: slide 0.5s forwards;
    animation-delay: 0s;
}

.hmenu-dropdown.diagonal {
    justify-content: space-evenly;
    width: 400px;
    height: 200px;
}

.hmenu-dropdown.vertical {
    justify-content: space-around;
    width: 300px;
    height: 400px;
}

@-webkit-keyframes slide {
    100% { right: -120%; }
}

@keyframes slide {
    100% { right: -120%; }
}

.dropdown-menulinks {
    text-decoration: none;
    font-size: 18px;
    color: white;
    background-image: linear-gradient(#ffffff, #ffffff);
    background-size: 0% 0.1em;
    background-position-y: 100%;
    background-position-x: 0%;
    background-repeat: no-repeat;
    transition: background-size 0.2s ease-in-out;
    margin-left: 3vw;
}

.dropdown-menulinks:hover {
    background-size: 100% 0.1em;
}


.title-container {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    position: relative;
    align-items: center;
    overflow: hidden;
}

.title-container-start {
    position: absolute;
    top: 0;
    width: 100%;
}

.title-container-imgcontainer {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    z-index: 0;
}

.title-container-imgcontainer-img {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0 auto;
    object-fit: cover;
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
}

.title-container-description {
    background-color: transparent;
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.title-container-description-title {
    background-color: inherit;
    font-size: 6vw;
    color: white;
    font-weight: bold;
}

.title-container-description-text {
    background-color: inherit;
    color: white;
    font-size: 3vw;
    font-weight: 600;
    padding-top: 40px;
}

.title-container-description-btncontainer {
    display: flex;
    background-color: green;
    width: 190px;
    height: 50px;
    margin-top: 40px;
    border-radius: 99px;
    justify-content: center;
    align-items: center;
}

.title-container-description-btncontainer-ref {
    text-decoration: none;
    color:white;
}

.title-container-jump {
    width: 100%;
    height: 60px;
    background-color: transparent;
    position: absolute;
    bottom: 0;
}
.title-container-description-btncontainer:hover {
    background-color: rgb(0, 153, 0);
}

@media screen and (min-width : 2000px) {
    .title-container-description-btncontainer {
        width: 10vw;
        height: 5.5vh;
    }
    .title-container-description-btncontainer-ref {
        font-size: 1vw;
    }
    .title-container-jump {
        height: 6vh;
    }
}

.news-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
}


.news-container-title {
    background: white;
    width: 100%;
    height: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    text-align: center;
}

.news-container-imgcontainer {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.news-container-img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    margin: 0 auto;
    -webkit-filter: brightness(85%);
            filter: brightness(85%);
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 1;
}

.news-container-imginfo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 2;
    text-align: center;
}

.news-container-imginfo h2 {
    display: flex;
    color: white;
    font-size: 30px;
    flex-grow: 1;
    flex-basis: 0;
}

.news-container-imginfo p {
    display: flex;
    color: white;
    font-size: 20px;
    flex-grow: 1;
    flex-basis: 0;
    margin-top: 20px;
}

.news-container-imginfo-btncontainer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    background-color: green;
    width: 190px;
    height: 50px;
    border-radius: 99px;
}

.news-container-imginfo-btncontainer a {
    text-decoration: none;
    color: white;
}

.news-container-imginfo-btncontainer:hover {
    background-color: rgb(0, 153, 0);
}

.news-container-info {
    background: white;
    width: 100%;
    height: 40%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 30px;
}

.news-container-info h2 {
    color:black;
}

.news-container-info p {
    margin-top: 30px;
    color: black;
    text-align: center;
    font-size: 20px;
}

.news-container-jump {
    width: 100%;
    height: 60px;
    background-color: transparent;
    position: absolute;
    bottom: 0;
}


@media screen and (max-width : 1000px) {
    .news-container-title {
        font-size: 3vw;
    }

    .news-container-imginfo h2 {
        font-size: 5vw;
    }

    .news-container-imginfo p {
        font-size: 3.5vw;
    }

    .news-container-info {
        font-size: 3.5vw;
    }

    .news-container-info p {
        font-size: 3vw;
    }

    h2.diagonal {
        font-size: 3vw;
    }

    p.diagonal {
        font-size: 2vw;
    }

    .news-container-imginfo-btncontainer.diagonal {
        width: 150px;
        height: 30px;
        font-size: 2vw;
    }
}

@media screen and (min-width : 2000px) {
    .news-container-title {
        font-size: 2vw;
    }

    .news-container-imginfo h2 {
        font-size: 2vw;
    }

    .news-container-imginfo p {
        font-size: 1.5vw;
    }

    .news-container-info {
        font-size: 2vw;
    }

    .news-container-info p {
        font-size: 1.5vw;
    }

    .news-container-imginfo-btncontainer{
        width: 10vw;
        height: 5.5vh;
        font-size: 1vw;
    }
    .news-container-jump {
        height: 6vh;
    }
}
.gallery-container {
    position: relative;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: black;
    width: 100vw;
    padding-bottom: 1vh;;
}

.img-row {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 1vh;;
    padding-right: 1vh;;
}

.row2 {
    height: 50%;
}

.row3 {
    height: 33.33%;
}

.row6 {
    height: 16.67%;
}

.col3 {
    width: 33.33%;
    height: 100%;
    padding-left: 1vh;;
}

.col2 {
    width: 50%;
    height: 100%;
    padding-left: 1vh;;
}

.col1 {
    width: 100%;
    height: 100%;
    padding-left: 1vh;;
}

.img-container {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.gallery-jump {
    position: absolute;
    width:100%;
    height: 100%;
    bottom:0;
